import React from 'react';
export const JourneyDetail= (props) => {
    return(
        <div className="items-journey">
        <div className="textalign-left color1">
          Journey details
        </div>
        <div className="textalign-left color2">
          {props.value.Source_location+" - "+ props.value.Destination}
        </div>
        <div className="textalign-left color2">
          {props.value.Num_travellers +" Persons, "+props.value.Car_type}
        </div>
        <div className="textalign-right color2">
            <button className="editButton" type="button" onClick={()=>props.fun(props.values)}>              
            <img className ="editImage" src="image/edit_icon.png"/>Edit
            </button>
          
        </div>
        </div>
    );


}