import { Formik, Form, Field, ErrorMessage } from "formik";
import React from 'react';
export const InputComponent= (props) => {
    if(props.class.length==3){
    return (
        <div className={props.class[0]} id={props.id[0]}>
        <div className={props.class[1]} id={props.id[1]}>{props.text}</div>
        {props.value?<Field name={props.value} className={props.class[2]}  id={props.id[2]}/>:null}
        {props.error?<div><ErrorMessage name={props.value}/></div>:null}
        </div>
    );
    }
    if(props.class.length==2){
        return (
        <div className={props.class[0]} id={props.id[0]}>
        {props.value?<Field name={props.value} className={props.class[1]}  id={props.id[1]}/>:null}
        {props.error?<div><ErrorMessage name={props.value}/></div>:null}
        </div> 
        );
    };

}