import React from 'react';
import * as Yup from "yup";
import "../../css/styles.css";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { JourneyDetail } from "../Tools.component/journeyDetails.component.js";
import { InputComponent } from "../Tools.component/inputComponent.component.js";
import { ButtonComponent } from "../Tools.component/buttonComponent.component.js";
export const StepTwo = (props) => {
    const stepTwoValidationSchema = Yup.object({
        Amount: Yup.string().required().label("Amount"),
        Number: Yup.string().required().matches(/^[0-9]+$/, "Must be only digits")
        .min(12, 'Must be exactly 10 digits')
        .max(12, 'Must be exactly 10 digits').label("Enter your Number"),
        Name: Yup.string().required().label("Enter Your Name"),
        Remarks: Yup.string().required().label("Remarks( Optional )"),
      });
    const handleSubmit = (values) => {
      props.next(values,true);
    };
  
    return (
      <Formik
        validationSchema={stepTwoValidationSchema}
        initialValues={props.data}
        onSubmit={handleSubmit}
      >
        {({ values }) => (
          
          <Form>
              <JourneyDetail value={props.data} values={values} fun={props.prev}/>
              <InputComponent class={["items-journey", "amount-input"]} id={["amount","",""]} error={false} text={""} value={"Amount"} type={"text"}/>
              <InputComponent class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox5","",""]} error={true} text={"Enter your Number"} value={"Number"} type={"text"}/>
              <InputComponent class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox6","",""]} error={false} text={"Enter Your Name"} value={"Name"} type={"text"}/>
              <InputComponent class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox7","",""]} error={false} text={"Remarks( Optional )"} value={"Remarks"} type={"text"}/>
              <ButtonComponent  class={"item-btn-f1" } id={"btn-f2"} text={"Final Submit"}/>
          </Form>
        )}
      </Formik>
    );
  };
  