import { StepOne } from './components/Form.component/component1.js';
import { StepTwo} from './components/Form.component/component2.js';
import { useState } from "react";
import "./css/styles.css";

export default function App() {
  const [data, setData] = useState({
    Source_location: "",
    Destination: "",
    Car_type: "",
    Num_travellers:0,
    Amount:"$",
    Name:"",
    Number:"+91",
    Remarks:"",
  });
  const [currentStep, setCurrentStep] = useState(0);


  const handleNextStep = (newData, final = false) => {
    setData((prev) => ({ ...prev, ...newData }));

    if (final) {
      return;
    }

    setCurrentStep((prev) => prev + 1);
  };

  const handlePrevStep = (newData) => {
    setData((prev) => ({ ...prev, ...newData }));
    setCurrentStep((prev) => prev - 1);
  };

  const steps = [
    <StepOne next={handleNextStep} data={data} />,
    <StepTwo next={handleNextStep} prev={handlePrevStep} data={data} />
  ];

  console.log("data", data);

  return (<div className="App">
            <div className="grid-container" >
            <div className="grid-item-logo">
                  <img className ="max-width: 100%" src="image/Logo.PNG"/>
            </div>
            <div className="grid-item-box">
                  <div className="item-state-text">Place your Bid ({currentStep}/2 Step)</div>
            </div>
            {steps[currentStep]}
            </div>
          </div>);
}


