import React from 'react';
import * as Yup from "yup";
import "../../css/styles.css";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { InputComponent } from "../Tools.component/inputComponent.component.js";
import { ButtonComponent } from "../Tools.component/buttonComponent.component.js";
function validateNum(value) {
  let error;
  const valu=parseInt(value)
  if (valu>5) {
    error = 'Type value less than 5';
  }
  return error;
}
export const StepOne = (props) => {
    const stepOneValidationSchema = Yup.object({
        Source_location: Yup.string().required().label("Source Location *"),
        Destination: Yup.string().required().label("Destination *"),
        Car_type: Yup.string().required().label("Enter Car Type *"),
        Num_travellers:Yup.number().required().label("Number Of Travellers *"),
    });
    const handleSubmit = (values) => {
      props.next(values);
    };
    return (
  
      <Formik
        validationSchema={stepOneValidationSchema}
        initialValues={props.data}
        onSubmit={handleSubmit}
      >
        {() => (
          <Form>
           <InputComponent  class={["boxInput", "textalign-left color1","inputSize"]} id={["","",""]} error={true} text={"Source Location *"} value={"Source_location"} type={"text"}/>  
           <InputComponent class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox2","",""]} error={true} text={"Destination *"} value={"Destination"} type={"text"}/>
           <InputComponent class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox3","",""]} error={true} text={"Enter Car Type"} value={"Car_type"} type={"text"}/>
           <InputComponent  class={["boxInput", "textalign-left color1","inputSize"]} id={["ibox4","",""]} error={true} text={"Number Of Travellers *"} value={"Num_travellers"} type={"Select"}/>
           <ButtonComponent  class={"item-btn-f1" } id={""} text={"Enter Bid Details"}/>
          </Form>
        )}
      </Formik>
    );
  };
  